@import "mixins.scss";

main {
    background-color:$blaufons;
    .paragraph--view-mode--home-superior:before {
        background-image: url(/themes/custom/asterics/images/assets-01.svg);
        background-repeat: no-repeat;
        background-size: 100% 100%;
        content: "";
        display: block;
        position: absolute;
        top: 56px;
        right: 20%;
        width: 100%;
        height: 100%;
        z-index: 0;
    }
    .paragraph--view-mode--home-superior {
        text-align:center;
        padding:45px 0;
        .paragraph--type--text {
            text-align:left;
            width:55%;
            float:right;
            clear:both;
            padding-right:15%;
            margin-bottom:50px;
        }
        .paragraph--view-mode--news {
            clear:both;
            text-align:left;
            padding:120px 0 45px 0;
            position: relative;
        }
    }
    .paragraph--view-mode--home-inferior:before {
        background-image: url(/themes/custom/asterics/images/assets-02.svg);
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: right;
        content: "";
        display: block;
        position: absolute;
        bottom: 130px;
        width: 50%;
        height: 100%;
        z-index: 1;
    
    }
    .paragraph--view-mode--home-inferior {
        position: relative;
        background:$blanc;
        color:$blaufons;
        text-align:center;
        padding:50px 0 10px 0;
    }
    #block-link-home-observation-form {
        padding:50px 0;
        text-align:center;
        color:$negre;
        p {
            font-weight:400;
            font-size:24px;
        }
        
    }
    #aladin-lite-div {
        margin:0 auto;
    }
    // VISTA NEWS & EVENTS //
    .views-element-container {
        .news {
            display: flex;
            flex-wrap: wrap;
            justify-content:space-evenly;
        }
    }
    // TEASERS NEWS & EVENTS //
    article.teaser {
        width:290px;
        padding:40px 0 100px 0;
        margin-left:50px;
        background: -moz-linear-gradient(top, rgba(35,95,135,1) 0%, rgba(35,95,135,0) 100%); /* FF3.6-15 */
        background: -webkit-linear-gradient(top, rgba(35,95,135,1) 0%,rgba(35,95,135,0) 100%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(to bottom, rgba(35,95,135,1) 0%,rgba(35,95,135,0) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#235f87', endColorstr='#00235f87',GradientType=0 );
        .teaser-wrapper {
            position:relative;
            right:50px;
            .category {
                @include genericlink($buttonlink);
            }
            .title {
                padding:36px 0;
                font-weight:900;
                font-size:30px;
            }
            .info {
                font-weight:300;
                font-size:18px;
                display:inline-block;
                .wrapper {
                    display:inline;
                    display:inline-block;
                    .date-item {
                        display:inline;
                        &:after {
                            content:"-";
                        }
                    }
                    .date-item:last-child {
                        &:after {
                            content:"";
                        }
                    }

                }
            }
        }

    }
    // TEASERS NEWS & EVENTS //
}